
import { useState } from "react";
import Link from "next/link";
import { MenuIcon, XIcon } from "@heroicons/react/outline";

function Header() {
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => setMenuOpen(!menuOpen);

  return (
    <header className="bg-[#37353E] text-[#D3DAD9]">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 flex justify-between items-center h-16">
        {/* Logo */}
        <div className="flex-shrink-0 text-xl font-bold">
          <Link href="/">Locafind</Link>
        </div>

        {/* Desktop Navigation */}
        <nav className="hidden md:flex space-x-6 items-center">
          <Link href="/" className="hover:text-[#715A5A]">Home</Link>
          <Link href="#categories" className="hover:text-[#715A5A]">Categories</Link>
          <Link href="#add-listing" className="bg-[#715A5A] text-[#D3DAD9] px-4 py-2 rounded hover:bg-[#44444E]">
            Add Listing
          </Link>
        </nav>

        {/* Mobile Hamburger */}
        <div className="md:hidden">
          <button onClick={toggleMenu} className="focus:outline-none">
            {menuOpen ? (
              <XIcon className="h-6 w-6 text-[#D3DAD9]" />
            ) : (
              <MenuIcon className="h-6 w-6 text-[#D3DAD9]" />
            )}
          </button>
        </div>
      </div>

      {/* Mobile Menu */}
      {menuOpen && (
        <div className="md:hidden bg-[#37353E] px-4 pt-2 pb-4 space-y-2">
          <Link href="/" className="block hover:text-[#715A5A]">Home</Link>
          <Link href="#categories" className="block hover:text-[#715A5A]">Categories</Link>
          <Link href="#add-listing" className="block bg-[#715A5A] text-[#D3DAD9] px-4 py-2 rounded hover:bg-[#44444E]">
            Add Listing
          </Link>
        </div>
      )}
    </header>
  );
}

export default Header;